/* 
Animation after Preloader 
    TITLE - _bem-styles.scss {
    h1 - 6.4s [before - 6s; after - 6.2s]
    }
    UNDET TITLE TEXT - {
    h2 - 6.55s
    h3 - 6.8s
    p - 6.9s
    :before - 8s
    }
*/

#hero{
    background: $maincolor;
    width: 100%;
    max-width: 100%;
    padding: 150px 0 200px;
    position: relative;
    height: 100vh;
    .hero-screen{
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        max-width: 1240px;
        margin: 0 auto;
        position: relative;
        height: 100%;
        .hero-img{
            flex: 0 0 45%;
            order: 2;
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            .hero-img-wrapper{
                display: flex;
                flex-wrap: wrap;
                width: 100%;
                align-items: center;
                justify-content: space-between;
                .lap-wrapper{
                    position: relative;
                    &:before{
                        position: absolute;
                        content: '';
                        bottom: 0;
                        left: 0;
                        width: 258px;
                        height: 180px;
                        background: transparent;
                        transform: rotate(-33deg) translate(28px, 23px) matrix(1, 0, .4, 1, 17, 12);
                        border-radius: 12px;
                        animation: lapShadow 2s infinite;

                    }
                    .lap{
                        width: 390px;
                        height: 410px;
                        animation: heroLap 2s infinite;
                    }
                }
                .phone-wrapper  {
                    position: relative;
                    &:before{
                        position: absolute;
                        content: '';
                        bottom: 0;
                        left: 0;
                        width: 28px;
                        height: 130px;
                        background: transparent;
                        transform: rotate(-41deg) translate(-36px, 72px) matrix(1, 0, 0.4, 1, 17, 12);
                        border-radius: 12px;
                        animation: lapShadow 2s infinite;
                    }
                    .phone{
                        width: 130px;
                        height: 300px;
                        animation: heroLap 2s  infinite;
                    }
                }
            }
        }
        .hero-text{
            order: 1;
            flex: 0 0 55%;
            display: flex;
            position: relative;
            &:before{
                position: absolute;
                content: '';
                min-height: 1px;
                width: 70%;
                right: 150px;
                top: -300px;
                background: $maincolor;
                animation: before 1s 8s forwards;
            }
            .page-article{
                h2{
                    font-size: 65px;
                    color: $secondarycolor;
                    line-height: 1.1;
                    animation: articleTextAppearance 1s 6.55s forwards;
                    opacity: 0;
                    font-weight: 500;
                }
                h3{
                    font-size: 55px;
                    color: $textcolor;
                    line-height: 1.1;
                    animation: articleTextAppearance 1s 6.7s forwards;
                    opacity: 0;
                    font-weight: 500;
                }
                .animate-text{
                    animation: articleTextAppearance 1s 6.9s forwards;
                }
            }
        }
    }


}


@keyframes lapShadow{
    from{box-shadow: inset 0px -34px 65px -12px $secondarycolor;}
    50%{box-shadow: none;}
    to{box-shadow: inset 0px -34px 65px -12px $secondarycolor;}
}
@keyframes heroLap{
    from{transform: translateY(10px);}
    50%{transform: translateY(0);}
    to{transform: translateY(10px);}
}
@keyframes before{
    from{
        min-height: 1px;
    }
    1%{
        background: $darkenmaincolor;
    }

    to{
        min-height: 100vh;
        background: $darkenmaincolor;
    }
}